<template>
  <div id="addDepartment">
    <v-row justify="center">
      <v-icon title="创建部门" class="ml-4" v-text="icon" @click.stop="isSelectDept" />
      <v-dialog v-model="dialog" persistent max-width="600px">
        <!-- <template v-slot:activator="{ on, attrs }">
          <v-btn color="primary" dark v-bind="attrs" v-on="on" class="ml-3 mr-4 mt-1 mb-1" wi>增加部门</v-btn>
        </template>-->
        <v-card>
          <v-card-title>
            <span class="headline">添加部门</span>
          </v-card-title>
          <v-card-text>
            <v-form ref="form" v-model="valid" :lazy-validation="lazy">
              <v-row>
                <!-- <v-col cols="12" sm="6" md="4">
                  <v-text-field label="Legal first name*" required></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field
                    label="Legal middle name"
                    hint="example of helper text only on focus"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field
                    label="Legal last name*"
                    hint="example of persistent helper text"
                    persistent-hint
                    required
                  ></v-text-field>
                </v-col>-->

                <v-col cols="12">
                  <v-text-field
                    :rules="nameRules"
                    label="部门名称(*必填)"
                    required
                    v-model="departmentObj.departmentName"
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    :rules="nameRules"
                    label="部门编码(*必填)"
                    required
                    v-model="departmentObj.departmentCode"
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    :rules="nameRules"
                    label="排序号(*必填)"
                    required
                    v-model="departmentObj.numIndex"
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field label="部门描述" aria-multiline v-model="departmentObj.description" />
                </v-col>
              </v-row>
            </v-form>
            <!-- <small>*indicates required field</small> -->
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="dialog = false">关闭</v-btn>

            <v-btn
              color="blue darken-1"
              :disabled="!valid"
              text
              @click="doSave(parentUniqueCode)"
            >保存</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </div>
</template>

<script>
export default {
  data: () => ({
    color: "orange",
    icon: "mdi-plus-thick",

    lazy: false,
    valid: true,
    dialog: false,

    departmentObj: {
      departmentName: "",
      departmentCode: "",
      numIndex: "",
      description: ""
    },

    //表单验证
    nameRules: [v => !!v || "该选项为必填字段"]
  }),
  props: {
    parentUniqueCode: {
      type: Object
    }
  },
  methods: {
    doSave(code) {
      if (!code) {
        //初始化加载不回调
      } else {
        this.$emit(
          "addOrg",
          this.departmentObj.departmentName,
          this.departmentObj.departmentCode,
          this.departmentObj.numIndex,
          this.departmentObj.description,
          code.uniqueCode
        );
      }
      this.dialog = false;
    },

    isSelectDept() {
      if (!this.parentUniqueCode) {
        this.dialog = false;
        this.$store.commit("SET_SNACK", {
          success: false,
          msg: "请先选择部门"
        });
      } else {
        this.dialog = true;
      }
    }
  }
};
</script>

